class Calculate{

    addBaseStats(equipList,list){
        var baseObj = {HP: 0, MP: 0, STR: 0, DEX: 0, AGI: 0, INT: 0, MND: 0, CHR: 0, VIT: 0};
        baseObj = this.combineAttributes(equipList,list,baseObj);
        return baseObj;
    }

    addMeleeStats(equipList,list){
        var meleeObj = {ATT: 0, ACC: 0, DA:0, TA: 0, QA: 0, CritHit: 0, CritDmg: 0, StoreTP: 0, DualW: 0, SubBlow: 0};
        meleeObj = this.combineAttributes(equipList,list,meleeObj);
        return meleeObj;
    }

    addMagicStats(equipList,list){
      var magicObj = {MAB: 0, MACC: 0, MDMG:0, BurstI: 0, BurstII: 0, MCritHit: 0, MCritDmg: 0, FastCast: 0, ConsMP: 0};
      magicObj = this.combineAttributes(equipList,list,magicObj);
      return magicObj;
  }

  combineAttributes(equipList, list, obj){
    var temp = obj;
    for(var i=0; i < equipList.length; i++){
      if(typeof equipList[i] !== 'undefined'){
        if(list in equipList[i]){
          for(var prop in equipList[i][list]){
            temp[prop] += (isNaN(Number.parseInt(equipList[i][list][prop])) ? 0 : Number.parseInt(equipList[i][list][prop]));
          } 
        }
      }
    }
    for(prop in temp){
      if(temp[prop] === 0){
        delete temp[prop];
      }
    }
  return temp;
  }

  parseAddGearString(str){
    var temp = '';
    var reg = ''
    var obj = {main: '', sub: '', range: '', ammo: '', head: '', neck: '', left_ear: '', right_ear: '', 
      body: '', hands: '', left_ring: '', right_ring: '', back: '', waist: '', legs: '', feet: ''};
      for(var prop in obj){
        reg = new RegExp(prop+'="([A-Za-z0-9\\s\\.+]*)"');
        temp = str.match(reg);
        if(temp === null || temp === ''){
          reg = new RegExp(prop+'={[\\s]*name="([A-Za-z0-9\\s\\.+\']*)"');
          temp = str.match(reg);
          if(temp === null || temp === ''){
            obj[prop] = 'Null';
          }else{
            obj[prop] = temp[1];
          }
        }else{
          obj[prop] = temp[1];
        }
      }
      return obj;
  }
}

export default Calculate;