import React, { Component } from 'react';

class InputBox extends Component {
    constructor(props){
        super(props);
        this.inputItem = React.createRef();
        this.itemNames = [];
    }

    componentWillMount(){
        var temp = []
        for(var i=0; i < this.props.data.length; i++){
          temp[i] = this.props.data[i].name;
        }
        temp.sort();
        this.itemNames[0] = 'Null';
        var x = 1;
        for( i=0; i < temp.length; i++){
            if(temp[i] !== 'Null'){
                this.itemNames[x] = temp[i];
                x++;
            }
        }
    }

    handleChange = (event) => {
        var item = event.target.value;
        this.props.change(this.props.data.find(function(e){
            if(e.name === item){
                return e;
            }
        }),this.props.type);
    }

    render() {
      return (
        <div>
            <div className="inputBox text-center" >
                <select value={this.props.currentItem.name} ref={this.inputItem} className="ffxiInput" id={'ddl' + this.props.type} onChange={this.handleChange}>
                    {this.itemNames.map((equip) => 
                    <option key={equip} value={equip}>{equip}</option>
                    )}
                </select>
                
            </div>
           
        </div>
      );
    }
}
  
export default InputBox;