import React, { Component } from 'react';
import './css/App.css';
import EquipBox from './EquipBox';
import AddGear from './AddGear';
import StatBox from './StatBox';
import InputBox from './InputBox';
import Calculate from './Calculate';
import data from './BuildData';


class EquipmentList extends Component {
  constructor(props){
    super(props);
    this.state = {
      baseStats:  {},
      meleeStats: {},
      magicStats: {},
      isVisible: false,
      main: data.main[0],  sub: data.main[0],     range: data.range[0],      ammo: data.ammo[0], 
      head: data.head[0],  neck: data.neck[0],    left_ear: data.ear[0],     right_ear: data.ear[0], 
      body: data.body[0],  hands: data.hands[0],  left_ring: data.ring[0],   right_ring: data.ring[0], 
      back: data.back[0],  waist: data.waist[0],  legs: data.legs[0],        feet: data.feet[0]
    }
  }

  loadInputSet = (str) => {
    var cal = new Calculate();
    var obj = cal.parseAddGearString(str);
    var temp = '';
    for(var prop in obj){
      if(prop === 'left_ring' || prop === 'right_ring'){
        temp = 'ring';
      }else if(prop === 'left_ear' || prop === 'right_ear'){
        temp = 'ear'
      }else{
        temp = prop;
      }

      if(obj[prop] == 'Null'){
        obj[prop] = data.main[0];
      }else{
        obj[prop] = data[temp].find(function(e){
          if(e.name === obj[prop]){
            return e;
          }
        });
      }
        if(obj[prop] === '' || obj[prop] === null || obj[prop] === undefined){
          obj[prop] = data.main[0];
        }
      }

      var obj2 = this.buildNewTotal(obj);
      for(prop in obj2){
        obj[prop] = obj2[prop];
      }
      this.setState(obj);
  }


  changeItem = (e, type) => {
    var copy = this.state;
    copy[type] = e;
    copy = this.buildNewTotal(copy);
    this.setState(copy);
  }

  togglePanel = () => {
    this.setState(prevState => ({isVisible: !prevState.isVisible}));
  }

  buildNewTotal = (obj) => {
    var cal = new Calculate();
    var equipList = [obj.main, obj.sub,   obj.range,     obj.ammo, 
                    obj.head,  obj.neck,  obj.left_ear,  obj.right_ear, 
                    obj.body,  obj.hands, obj.left_ring, obj.right_ring, 
                    obj.back,  obj.waist, obj.legs,      obj.feet];
    obj.baseStats = cal.addBaseStats(equipList, 'baseStats');
    obj.meleeStats = cal.addMeleeStats(equipList,'meleeStats');
    obj.magicStats = cal.addMagicStats(equipList, 'magicStats');    
    return obj;
  }


  render() {
    return (
      <div className="container-fluid EquipmentListHolder">
        <div className="EquipmentList container-fluid text-center" style={{width:'80%',backgroundImage: "url('./img/ffxiEquipBg.png')"}}>
          <div className="row equal spacedRow equal">
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'main'} currentItem={this.state.main}/>
              <InputBox data={data.main} type={'main'} change={this.changeItem} currentItem={this.state.main}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'sub'} currentItem={this.state.sub}/>
              <InputBox data={data.main} type={'sub'} change={this.changeItem} currentItem={this.state.sub}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'range'} currentItem={this.state.range}/>
              <InputBox data={data.range} type={'range'} change={this.changeItem} currentItem={this.state.range}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'ammo'} currentItem={this.state.ammo}/>
              <InputBox data={data.ammo} type={'ammo'} change={this.changeItem} currentItem={this.state.ammo}/>
            </div>  
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'head'} currentItem={this.state.head}/>
              <InputBox data={data.head} type={'head'} change={this.changeItem} currentItem={this.state.head}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'neck'} currentItem={this.state.neck}/>
              <InputBox data={data.neck} type={'neck'} change={this.changeItem} currentItem={this.state.neck}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'left_ear'} currentItem={this.state.left_ear}/>
              <InputBox data={data.ear} type={'left_ear'} change={this.changeItem} currentItem={this.state.left_ear}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'right_ear'} currentItem={this.state.right_ear}/>
              <InputBox data={data.ear} type={'right_ear'} change={this.changeItem} currentItem={this.state.right_ear}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'body'} currentItem={this.state.body}/>
              <InputBox data={data.body} type={'body'} change={this.changeItem} currentItem={this.state.body}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'hands'} currentItem={this.state.hands}/>
              <InputBox data={data.hands} type={'hands'} change={this.changeItem} currentItem={this.state.hands}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'left_ring'} currentItem={this.state.left_ring}/>
              <InputBox data={data.ring} type={'left_ring'} change={this.changeItem} currentItem={this.state.left_ring}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'right_ring'} currentItem={this.state.right_ring}/>
              <InputBox data={data.ring} type={'right_ring'} change={this.changeItem} currentItem={this.state.right_ring}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'back'} currentItem={this.state.back}/>
              <InputBox data={data.back} type={'back'} change={this.changeItem} currentItem={this.state.back}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'waist'} currentItem={this.state.waist}/>
              <InputBox data={data.waist} type={'waist'} change={this.changeItem} currentItem={this.state.waist}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'legs'} currentItem={this.state.legs}/>
              <InputBox data={data.legs} type={'legs'} change={this.changeItem} currentItem={this.state.legs}/>
            </div>
            <div className="col-lg-3 col-md-6 col-sm-6">
              <EquipBox type={'feet'} currentItem={this.state.feet}/>
              <InputBox data={data.feet} type={'feet'} change={this.changeItem} currentItem={this.state.feet}/>            
            </div>
          </div>
          {!this.state.isVisible &&<button className="btn btn-primary" style={{marginBottom:'5px'}} onClick={this.togglePanel}>Add Your Gear</button>}
          {this.state.isVisible && <AddGear loadGSSet={this.loadInputSet} btnClose={this.togglePanel} />}
        </div>
        <div className="row">
        <StatBox title={'Base Stats'} stats={this.state.baseStats} />
        <StatBox title={'Melee Stats'} stats={this.state.meleeStats} />
        <StatBox title={'Magic Stats'} stats={this.state.magicStats} />
      </div>
    </div>
    );
  }
}

export default EquipmentList;
