import React, { Component } from 'react';

class AddGear extends Component {
    constructor(props){
        super(props);
        this.itemString = '';
    }

    updateText = (e) => {
        this.itemString = e.target.value;
    }

    handleClick = () => {
        this.props.loadGSSet(this.itemString);
    }


    render() {
      return (
        <div className="row">
            <div className="col-lg-8 col-md-8 col-sm-12 col-xs-12 offset-lg-2 offset-md-2">
                <textarea id="txtAddGear" onChange={this.updateText} style={{width:'100%',height:'250px',fontSize:'12px'}}></textarea>
                <br />
                <button className="btn btn-primary" style={{marginRight:'3px'}} onClick={this.handleClick}>Equip</button>
                <button className="btn btn-primary" style={{marginLeft:'3px'}} onClick={this.props.btnClose}>Close</button>
            </div>
        </div>
      );
    }
}
  
export default AddGear;