import React, { Component } from 'react';
import ImageBox from './ImageBox';

class EquipBox extends Component {

    render() {
      return (
        <div className="" style={{padding:'5px'}}>
          <div className="ffxiEquipBox">
            <div className="equipmentHolder">
              <ImageBox imageUrl={this.props.currentItem.name.replace(/ /g,"_") + '_icon.png'} type={this.props.type} />
              <div className="itemName text-center"><span>{this.props.currentItem.name}</span></div>         
            </div> 
          </div>
        </div>     
      );
    }
  }
  
  export default EquipBox;